import "./css/main.css";

const CLASS_NAME_MAIN_CANVAS: string = "MainCanvas";
const ID_COLOR_PICKER: string = "colorPicker";
const ID_STROKE_WIDTH: string = "strokeWidth";
const ID_BTN_SAVE: string = "btnSave";
const ID_BTN_UNDO: string = "btnUndo";

type point = {
    x: number,
    y: number
};

function pointsToString(points: Array<point>): string {
    let pointsString: string = "";
    points.forEach(p => {
        pointsString = pointsString.concat(p.x.toString(), ',', p.y.toString(), ' ');
    });
    return pointsString;
}

document.addEventListener("DOMContentLoaded", () => {
    const mainCanvas: SVGElement | null = document.querySelector(`.${CLASS_NAME_MAIN_CANVAS}`);
    const colorPicker: HTMLInputElement | null = document.querySelector(`#${ID_COLOR_PICKER}`);
    const strokeWidthChoser: HTMLInputElement | null = document.querySelector(`#${ID_STROKE_WIDTH}`);
    const btnSave: HTMLInputElement | null = document.querySelector(`#${ID_BTN_SAVE}`);
    const btnUndo: HTMLInputElement | null = document.querySelector(`#${ID_BTN_UNDO}`);

    if (mainCanvas && colorPicker && strokeWidthChoser && btnSave && btnUndo) {
        const offsetY: number = mainCanvas.getBoundingClientRect().y;
        mainCanvas.addEventListener("mousedown", (ev: MouseEvent) => {
            const lineToDraw: HTMLElement = document.createElement("polyline");
            lineToDraw.setAttribute("fill", "none");
            lineToDraw.setAttribute("stroke", colorPicker.value);
            lineToDraw.setAttribute("stroke-width", strokeWidthChoser.value);

            let redraw: boolean = false;

            const points: Array<point> = Array();

            points.push({
                x: ev.x,
                y: ev.y - offsetY
            });

            console.log(points);

            const mouseMoveHandler = function (event: MouseEvent) {
                points.push({
                    x: event.x,
                    y: event.y - offsetY
                });
                console.log(points);

                if (redraw && mainCanvas.lastChild)
                    mainCanvas.removeChild(mainCanvas.lastChild);
                else
                    redraw = true;

                lineToDraw.setAttribute("points", pointsToString(points));
                mainCanvas.appendChild(lineToDraw);
                mainCanvas.innerHTML += "";
            };

            const mouseUpHandler = function (_event: MouseEvent) {
                console.log("Drawing finished.");
                console.log(points);

                mainCanvas.removeEventListener("mousemove", mouseMoveHandler);
                mainCanvas.removeEventListener("mouseup", mouseUpHandler);
            };

            mainCanvas.addEventListener("mousemove", mouseMoveHandler);

            mainCanvas.addEventListener("mouseup", mouseUpHandler);
        });

        btnSave.addEventListener("click", (_ev: MouseEvent) => {
            const serializer = new XMLSerializer();
            let source = serializer.serializeToString(mainCanvas);

            //add name spaces.
            if(!source.match(/^<svg[^>]+xmlns="http\:\/\/www\.w3\.org\/2000\/svg"/)){
                source = source.replace(/^<svg/, '<svg xmlns="http://www.w3.org/2000/svg"');
            }
            if(!source.match(/^<svg[^>]+"http\:\/\/www\.w3\.org\/1999\/xlink"/)){
                source = source.replace(/^<svg/, '<svg xmlns:xlink="http://www.w3.org/1999/xlink"');
            }

            //add xml declaration
            source = '<?xml version="1.0" standalone="no"?>\r\n' + source;

            //convert svg source to URI data scheme.
            const url = "data:image/svg+xml;charset=utf-8," + encodeURIComponent(source);

            const body: HTMLBodyElement | null = document.querySelector("body");
            if (body) {
                const link: HTMLAnchorElement = document.createElement("a");
                link.download = "";
                link.href = url;
                body.appendChild(link);
                link.click();
                body.removeChild(link);
            }
        });

        btnUndo.addEventListener("click", (_ev: MouseEvent) => {
            if (mainCanvas.lastChild)
                mainCanvas.removeChild(mainCanvas.lastChild);
        });
    }
});